<template>
  <v-app>
    <Header 
      :langs="langs"
      @delete-local-strage="deleteLocalStrage" 
    />
    <v-main>
      <v-container>
        <router-view 
          :books="books" :users="users" :borrowing="borrowing" 
          @update-books="updateBooks" 
          @update-users="updateUsers"
          @add-borrowing="addBorrowing"
          @del-borrowing="delBorrowing"
        />
      </v-container>
    </v-main>
    <Footer/>
  </v-app>
</template>

<script>
import Header from '@/global/Header'
import Footer from '@/global/Footer'
const STRAGE_KEY_BOOKS = 'books'
const STRAGE_KEY_USERS = 'users'
const STRAGE_KEY_BORROWING = 'borrowing'

export default {
  name: 'App',

  components:{
    Header,
    Footer
  },

  data() {
    return {
      books: [],
      users: [],
      borrowing: [],
      langs : [ 'ja', 'en', 'it' ],

      default_books: [
        { isbn:'9788867582143', title:'I promessi sposi', image:'http://books.google.com/books/content?id=vIXxzQEACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api' },
        { isbn:'9780143106272', title:'Great Expectations', image:'http://books.google.com/books/content?id=IBspDwAAQBAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api' },
        { isbn:'9784094087871', title:'坊っちゃん', image:'http://books.google.com/books/content?id=6XV2lAEACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api' },
        { isbn:'9784003107638', title:'銀河鉄道の夜他十四篇(童話集)', image:'http://books.google.com/books/content?id=pMJmPwAACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api'},
        { isbn:'9788807901119', title:'Pinocchio', image:'http://books.google.com/books/content?id=QimZoAEACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api'},
        { isbn:'9781948132824', title:'The Adventures of Tom Sawyer', image:'http://books.google.com/books/content?id=72sitwEACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api'},
        { isbn: '9780316769532', title: 'The Catcher in the Rye', image: 'http://books.google.com/books/content?id=m3GzoAEACAAJ&printsec=frontcover&img=1&zoom=1&source=gbs_api' },
        { isbn: '9780544133228', title: 'Marcovaldo', image: 'http://books.google.com/books/content?id=iGA4mhtQWD8C&printsec=frontcover&img=1&zoom=1&edge=curl&source=gbs_api' },
        { isbn: '9784862392930', title: 'スティル・ライフ', image: 'http://books.google.com/books/content?id=m_UACgAAQBAJ&printsec=frontcover&img=1&zoom=1&edge=curl&source=gbs_api' },
        { isbn: '9788807813023', title: 'Novecento', image: 'http://books.google.com/books/content?id=gNZzQIE3EUAC&printsec=frontcover&img=1&zoom=1&edge=curl&source=gbs_api' },
        { isbn: '9780345803054', title: 'Breakfast at Tiffany\'s', image: 'http://books.google.com/books/content?id=UwWWh1WLn9EC&printsec=frontcover&img=1&zoom=1&edge=curl&source=gbs_api' },
    ],
      default_users: [
        { id:1, name:'Alessandro Manzoni', email:'amanzoni@dummy.com' },
        { id:2, name:'Charles Dickens', email:'cdickends@dummy.com' },
        { id:3, name:'夏目 漱石', email:'snatsume@dummy.com' },
      ],
      default_borrowing: [
        { book_isbn: '9788867582143', user_id: 1, create_datetime: '2022-05-24T01:04:49.813Z' },
        { book_isbn: '9780143106272', user_id: 1, create_datetime: '2022-05-24T01:04:49.813Z' }
      ]
    }
  },
  mounted() {
    this.$i18n.locale = this.langs[0]

    if (localStorage.getItem(STRAGE_KEY_BOOKS)) {
        try {
            this.books = JSON.parse(localStorage.getItem(STRAGE_KEY_BOOKS))
        } catch(e) {
            localStorage.removeItem(STRAGE_KEY_BOOKS)
        }
    }
    if ( !this.books.length ) {
      this.books = this.default_books
      this.saveBooks()
    }

    if (localStorage.getItem(STRAGE_KEY_USERS)) {
        try {
            this.users = JSON.parse(localStorage.getItem(STRAGE_KEY_USERS))
        } catch(e) {
            localStorage.removeItem(STRAGE_KEY_USERS)
        }
    }
    if ( !this.users.length ) {
      this.users = this.default_users
      this.saveUsers()
    }

    if (localStorage.getItem(STRAGE_KEY_BORROWING)) {
        try {
            this.borrowing = JSON.parse(localStorage.getItem(STRAGE_KEY_BORROWING))
        } catch(e) {
            localStorage.removeItem(STRAGE_KEY_BORROWING)
        }
    }
    if ( !this.borrowing.length ) {
      this.borrowing = this.default_borrowing
      this.saveBorrowing()
    }

  },
  methods:{
    updateBooks(e) {
      let index = this.books.findIndex((b) => b.isbn === e.isbn)
      this.books.splice( index, 1, e )
      this.saveBooks()
      //this.$router.push('/booksearch')
    },
    updateUsers(e) {
      let user_id = (e.id) ? e.id :(this.users.reduce((a, b) => a > b.id ? a : b.id, 0) + 1)
      let new_user = 
        {
          id: user_id,
          name: e.name,
          email: e.email
        }
      let index = this.users.findIndex((b) => b.id === user_id)  
      this.users.splice( index, 1, new_user )
      this.saveUsers()
      //this.$router.push('/usersearch')
    },
    addBorrowing(e) {
      this.borrowing = this.borrowing.concat( e )
      this.saveBorrowing()
      //this.$router.push('/borrowing')
    },    
    delBorrowing(e) {
      for (let i = 0; i < e.length; i++) {
            let index = this.borrowing.findIndex(o => o.book_isbn==e[i].isbn )
            this.borrowing.splice( index, 1 )
      }
      this.saveBorrowing()
      //this.$router.push('/returning')
    },

    saveBooks() {
      const parsed = JSON.stringify(this.books)
      localStorage.setItem(STRAGE_KEY_BOOKS, parsed)
    }, 
    saveUsers() {
      const parsed = JSON.stringify(this.users)
      localStorage.setItem(STRAGE_KEY_USERS, parsed)
    }, 
    saveBorrowing() {
      const parsed = JSON.stringify(this.borrowing)
      localStorage.setItem(STRAGE_KEY_BORROWING, parsed)
    }, 

    deleteLocalStrage() {
      const isDeleted = 'delete all data ?'
      if (window.confirm(isDeleted)) {
        localStorage.setItem(STRAGE_KEY_BOOKS, '')
        localStorage.removeItem(STRAGE_KEY_BOOKS)
        localStorage.setItem(STRAGE_KEY_USERS, '')
        localStorage.removeItem(STRAGE_KEY_USERS)
        localStorage.setItem(STRAGE_KEY_BORROWING, '')
        localStorage.removeItem(STRAGE_KEY_BORROWING)
        this.books = []
        this.users = []
        this.borrowing = []
        window.location.reload
      }
    },



  },  
};
</script>
