<template>
  <div>
      <p>返却</p>

      <v-container>
        <v-row>
          <v-col cols="6"><v-text-field label="ISBN" outlined v-model="isbn"></v-text-field></v-col>
          <v-col cols="6"><v-btn rounded large color="primary" @click="execReturning">返却</v-btn><v-spacer></v-spacer></v-col>          
        </v-row>
        <v-row>
          <v-col cols="8">

<v-card elevation="2">            
<v-simple-table v-if="new_returning_books.length">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="(book, index) in new_returning_books" :key="book.isbn">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td><v-btn rounded dark color="green" @click="cancel( index )">cancel</v-btn></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

          </v-col>
          <v-col cols="4">
          </v-col>
        </v-row>
      </v-container>

    <v-dialog v-model="dialog" max-width="300">
     <v-card>
        <v-card-text>{{ dialog_msg }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" rounded  @click="closeDialog">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
export default {
  name:'Returning',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            isbn: '',
            book: { isbn:'', title:'', image:'' },
            new_returning_books: [],
            dialog: false,
            dialog_msg: '',
        }
    },
  methods:{
    cancel( index ) {
      this.new_returning_books.splice( index, 1 )
    },
    execReturning() {
      if ( !this.new_returning_books.length ) {
        this.dialog_msg = '返却図書のISBNを入力して下さい。'
        this.dialog = true
        return
      }
      let r_books = this.new_returning_books
      this.$emit('del-borrowing', r_books)
      this.new_returning_books = []
      this.dialog_msg = '返却処理を実行しました。'
      this.dialog = true
      this.resetForm()
    },
    resetForm() {
      this.isbn = ''
      this.book =  { isbn:'', title:'', image:'' }
      this.new_returning_books =  []
    },
    closeDialog() {
      this.dialog = false

    },

  },
  watch: {
  
    'isbn': function( newValue ) {
      if (newValue.length !== 13 ) return

      let book_index = this.books.findIndex((b) => b.isbn === newValue)
      console.log('book_index: ' + book_index)
      if ( book_index == -1 ) {
          this.dialog_msg = '登録されていない図書です。'
          this.dialog = true
          return
      }

      let exist_borrowing = this.borrowing.some((o) => o.book_isbn === newValue) 
      console.log('exist_borrowing: ' + exist_borrowing)
      if ( !exist_borrowing) {
          this.dialog_msg = '貸し出されていない図書です。'
          this.dialog = true
          return        
      }

      this.book = this.books[ book_index]
      this.new_returning_books.push( this.books[ book_index] )
      //this.isbn = ''
    },

  },

}
</script>

