<template>
  <div>
   
    <p>{{  execType }}</p>
          <v-card elevation="2">

      <v-row>
      
      <v-col cols="8">
 <v-text-field label="ID" outlined v-model="user.id" readonly></v-text-field>
  <br>
  <v-text-field label="Name" outlined v-model="user.name"></v-text-field>
  <br>
    <v-text-field label="E-mail" outlined v-model="user.email"></v-text-field>
  
      </v-col>
      <v-col cols="8">
          <v-btn rounded large color="primary" dark @click="updateUser">{{  execType }}</v-btn>

      </v-col>

      </v-row>
          </v-card>


    <v-dialog v-model="dialog" max-width="300">
     <v-card>
        <v-card-text>{{ dialog_msg }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" rounded  @click="closeDialog">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
export default {
    name: 'UserEdit',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            user: { id:0, name:'', email:'' },
            execType: '',
            dialog: false,
            dialog_msg : '',
            go_search: true,

        }
    },
  methods:{
    resetForm() {
      this.user = { id:0, name:'', email:'' }      
    },
    updateUser() {
        //console.log( this.user )

        let exist = this.users.some((u) => u.email==this.user.email )
        if ( !this.user.id && exist) {
          this.go_search = false
          this.dialog_msg = 'このメールアドレスは、すでに登録済みです。'
          this.dialog = true
          return
        }

        this.$emit('update-users', this.user )
        this.dialog_msg = this.execType + '処理を実行しました。'
        this.dialog = true

    },
    closeDialog() {
      this.dialog = false
      if (this.go_search) this.$router.push('/usersearch')
    },    
  },
  mounted() {
      console.log( this.$route.params.id )
      let id = this.$route.params.id * 1
      if ( id !== 0 ) {
        this.execType = '更新'
        this.user = this.users.find((u) => u.id === id)
      } else {
        this.execType = '登録'
      }
  },

}
</script>

