<template>
  <div>
      <p>蔵書検索</p>
         <v-container>
           <v-row>
        <v-col
          cols="7"
          sm="4"
        >
      <v-text-field v-model="title"  label="title" outlined clearable append-outer-icon="mdi-magnify" @click:append-outer="searchBook"></v-text-field> 
        </v-col>
        <v-col cols="1" sm="1">
        <v-btn class="mx-2" fab dark large color="indigo" @click="editBook('0')">
      <v-icon>
        mdi-plus
      </v-icon>
    </v-btn>
        </v-col>
        <v-col cols="4"><v-spacer></v-spacer></v-col>
           </v-row>
 <v-row>
   <v-col cols="8">

<v-card elevation="2">
  <v-simple-table  v-if="search_result.length">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="book in search_result" :key="book.isbn" @click="editBook(book.isbn)">
          <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <!-- td><v-btn class="mx-2" fab dark small color="cyan" @click="editBook(book.isbn)"><v-icon>mdi-pencil</v-icon></v-btn></td -->
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

   </v-col>
   <v-col cols="4">
<v-spacer></v-spacer>


  </v-col>
 </v-row>
         </v-container>
  </div>
</template>

<script>
export default {
  name:'BookSearch',
     props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            title: '',
            search_result: []
        }
    },
  methods:{
    searchBook() {
      let reg_title = new RegExp( this.title, "i" )
      this.search_result = this.books.filter((b) => reg_title.test(b.title))
    },
    editBook(isbn) {
      this.$router.push(`/bookedit/${ isbn }`)
    }
  },
}
</script>

