<template>
  <div>
      <p>貸出</p>
      <v-container>
        <v-row>
          <v-col cols="6"><v-text-field label="user id" outlined v-model="user_id" @change="getUser"></v-text-field></v-col>
          <v-col cols="6">  {{ user.name }}<v-spacer></v-spacer></v-col>
        </v-row>
        <v-row>
          <v-col cols="6"><v-text-field label="ISBN" outlined v-model="isbn" @input="getBook"></v-text-field></v-col>
          <v-col cols="6"><v-btn rounded large color="primary" @click="execBorrowing">貸出</v-btn><v-spacer></v-spacer></v-col>          
        </v-row>
        <v-row>
          <v-col cols="8">

<v-card elevation="2">
<v-simple-table v-if="new_borrowing_books.length">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="(book, index) in new_borrowing_books" :key="book.isbn">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td><v-btn rounded dark color="green" @click="cancel( index )">cancel</v-btn></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

<br><br><br><br>

<v-card elevation="2">
<v-simple-table v-if="borrowed_books.length">
<caption>貸出中の図書</caption>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th class="text-left">Date</th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="book in borrowed_books" :key="book.isbn">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td>{{ book.date }}</td>
        </tr>
      </tbody>
    </v-simple-table>
</v-card>

          </v-col>
          <v-col cols="4">
          </v-col>
        </v-row>
      </v-container>
      
    <v-dialog v-model="dialog" max-width="300">
     <v-card>
        <v-card-text>{{ dialog_msg }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" rounded  @click="dialog = false">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

      
  </div>
</template>

<script>
export default {
  name:'borrowing',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            user_id: '',
            isbn: '',
            user: { id:0, name:'', email:'' },
            book: { isbn:'', title:'', image:'' },
            new_borrowing_books: [], 
            borrowed_books: [],
            dialog: false,
            dialog_msg: '',
        }
    },
  methods:{
    getUser() {
        if ( this.user_id.length == 0 ) return
        let id = parseInt( this.user_id )
        if ( isNaN(id) ) return
        let myuser = this.users.find((u) => u.id === id)
        if ( !myuser ) {
          this.dialog_msg = 'この利用者IDは登録されていません。'
          this.dialog = true
          return
        }
        
        this.user = myuser
        this.borrowed_books = this.getBorrowedBooksByUserId(id)      
    },

    execBorrowing() {
//      console.log('execBorrowing')
//      console.log('this.user_id.length: ' + this.user_id.length )
//      console.log('this.new_borrowing_books.length' + this.new_borrowing_books.length)
      if ( !this.user_id.length || !this.new_borrowing_books.length ) {
      this.dialog_msg = '利用者IDと貸出図書のISBNを入力して下さい。'
      this.dialog = true 
      return
      }
      let new_borrowing = []
      const userid = this.user.id
      const datetime = new Date()

      this.new_borrowing_books.forEach(function(book){
        console.log('foreach')
        console.log(book.title)
        console.log(userid)

          new_borrowing.push( {
          user_id: userid,
          book_isbn: book.isbn,
          create_datetime: datetime
          } )

      })
      this.$emit('add-borrowing', new_borrowing)
      this.dialog_msg = '貸出処理を実行しました。'
      this.dialog = true
      this.resetForm()
    },
    cancel( index ) {
      this.new_borrowing_books.splice( index, 1 )
    },
    resetForm() {
      this.user_id = ''
      this.isbn =  ''
      this.user =  { id:0, name:'', email:'' }
      this.book =  { isbn:'', title:'', image:'' }
      this.new_borrowing_books =  [] 
      this.borrowed_books = []
    },
    getBook() {
        console.log("getBook")
        let myisbn = this.isbn
        if ( myisbn.length !== 13 ) return

        console.log("abc")
        let exist = this.borrowing.some((e) => e.book_isbn===myisbn )
        console.log(myisbn + " exist in borrowing:" + exist)
        if ( exist ) {
          console.log("check borrowing")
          this.dialog_msg = '貸し出し中の図書です。'
          this.dialog = true
          this.book = ''
          return
        }

        let book_index = this.books.findIndex((b) => b.isbn === myisbn)
        console.log("book_index:" + book_index)        
        if ( book_index == -1 ) {
          console.log("check books")
          this.dialog_msg = '登録されていない図書です。'
          this.dialog = true
          return
        }
        this.book = this.books[book_index]
        console.log(this.book)
        this.new_borrowing_books.push( this.books[book_index] )
        //this.isbn = ''
    },

    getBorrowedBooksByUserId(userid) {
      let filterd_borrowing = this.borrowing.filter((o)=>o.user_id === userid)
      console.log('userid: ' + userid)
      console.log( 'typeof' + typeof(filterd_borrowing))
      console.log( 'toString:' + filterd_borrowing.toString())
      console.log('isarray: ' + Array.isArray( filterd_borrowing ))
      console.log('length:' + filterd_borrowing.length )
      console.log('filtered_borrowing:' + filterd_borrowing)

      let result = []
      let borrowed_info = {}
      for (let i = 0; i<filterd_borrowing.length; i++) {
        borrowed_info = this.books.find((b)=> b.isbn === filterd_borrowing[i].book_isbn)
        let mydate = new Date(filterd_borrowing[i].create_datetime)
        borrowed_info.date =  mydate.getFullYear() + '/' + (mydate.getMonth() + 1) + '/' + mydate.getDate()
        result.push( borrowed_info )
      }
      
      return result
    },

  },
  watch: {
    /*
      'user_id': function( newValue ) {
        if (newValue.length < 1) return
        let id = parseInt(newValue)
        if ( isNaN(id) ) return
        let myuser = this.users.find((u) => u.id === id)
        if ( !myuser ) {
          this.dialog_msg = 'この利用者IDは登録されていません。'
          this.dialog = true
          return
        }
        
        this.user = myuser
        this.borrowed_books = this.getBorrowedBooksByUserId(id)
      },
*/
/*      
      'isbn': function( newValue ) {
        if ( newValue.length !== 13 ) return false
        this.book = this.books.find((b) => b.isbn === newValue)
        console.log( this.book )
        this.new_borrowing_books.push( this.book )
        this.isbn = ''
      },
*/
  },

}
</script>

