<script>
export default {
  name:'UserSearch',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            name: '',
            search_result: [],
        }
    },
  methods:{

    searchUser() {
      let reg_user = new RegExp( this.name, "i" )
      this.search_result = this.users.filter((u) => reg_user.test(u.name))      
    },
    editUser(id) {
      this.$router.push(`/useredit/${ id }`)
    },
  

  },
}
</script>

