<template>
  <div>
    <p>{{  execType }}</p>
          <v-card elevation="2">

      <v-row>
      
      <v-col cols="4">
          <v-img :src="book.image" width="200"></v-img>
      </v-col>
      <v-col cols="8">
      

  <v-text-field id="isbn" label="ISBN" outlined v-model="isbn" :readonly="editmode" @input="checkISBN"></v-text-field>
  <br>
  <v-text-field label="Title" outlined v-model="book.title"></v-text-field>
  <br>
  <v-btn rounded large color="primary" dark @click="updateBook">{{  execType }}</v-btn>
 
      </v-col>

      </v-row>
          </v-card>


    <v-dialog v-model="dialog" max-width="300">
     <v-card>
        <v-card-text>{{ dialog_msg }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" rounded  @click="closeDialog">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
export default {
    name:'BookEdit',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    data(){
        return {
            book: { isbn:'', title:'', image:'no-image.png' },
            editmode: false,
            isbn: '',
            execType: '',
            dialog: false,
            dialog_msg: '',
            go_search: true
        }
    },
  methods:{
    updateBook() {
        console.log( this.book )
        if ( !this.book.isbn || !this.book.title ) {
          this.go_search = false
          this.dialog_msg = 'ISBNを入力して下さい。'
          this.dialog = true
          return
        }
      this.$emit('update-books', this.book )
      this.dialog_msg = this.execType + '処理を実行しました。'
      this.dialog = true
      return

    },
    resetForm() {
      this.book =  { isbn:'', title:'', image:'no-image.png' }
      this.isbn =  ''      
    },
    closeDialog() {
      this.dialog = false
      if (this.go_search) this.$router.push('/booksearch')
    },
    checkISBN() {
        if ( this.editmode || this.isbn.length !== 13 ) return
        let exist = this.books.some(b => b.isbn==this.isbn )
        if (exist) {
          this.go_search = false
          this.dialog_msg = 'すでに登録済みです。'
          this.dialog = true
          return
        }
        let isbn = this.isbn
        console.log(isbn)
        this.isbn = ''

        this.getBookInfo(isbn)

//        this.$forceUpdate()

    },
    async getBookInfo(isbn) {
        console.log( "getBookInfo" )

        this.searchResults = []
        const baseUrl = 'https://www.googleapis.com/books/v1/volumes?'
        const params = {
            q: `isbn:${ isbn }`,
            maxResults:1
        }
        const queryParams = new URLSearchParams(params)
        const res = await fetch(baseUrl + queryParams)
          .then(res=>res.json())
        if (res.totalItems == 0) {
          this.dialog_message = 'ISBN non found in Google Books'
          this.dialog = true
          return
        }
        console.log(res.items)
        const volumeInfo = res.items[0].volumeInfo
        console.log( volumeInfo )
        let title = (volumeInfo.title) ? volumeInfo.title : ''
        let image = (volumeInfo.imageLinks.thumbnail) ? volumeInfo.imageLinks.thumbnail : ''

        this.book.isbn = isbn
        this.book.title = title
        this.book.image = image
    },

  },

  mounted() {
      const isbn = this.$route.params.isbn
      if ( isbn == "0" ) {
        this.isbn = ''
        this.editmode = false
        this.execType = '登録'
      } else {
        console.log('isbn: ' + isbn)
        this.isbn = isbn
        this.editmode = true
        this.book = this.books.find((b) => b.isbn === isbn)
        console.log(this.book)
        this.execType = '更新'
      }
  },

}
</script>

